html {
	height: 100%;
}
* {
	box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: url(/facify/static/media/main_background.1548f2fe.svg);
	background-size: cover;
	background-repeat: no-repeat;
	text-align: center;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
	max-width: 800px;
	padding: 0 20px;
	margin: auto;
}

main {
	background: #ffffff;
	margin-bottom: 50px;
}

.stats {
	position: relative;
}

.stats::after {
	position: absolute;
	content: '';
	top: 0;
	right: 0;
	width: 0;
	height: 0;
	border-width: 0px 89px 79px 0;
	border-color: transparent #eaeaea transparent transparent;
	border-style: solid;
}

h1 {
	color: hsl(0, 0%, 57%);
	font-size: 1rem;
	margin: 0;
	padding: 10px 0;
}

.input-file--hidden {
	width: 0.1px;
	height: 0.1px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	z-index: -1;
}

.label-input-file {
	position: relative;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor: pointer;
	transition: 500ms;
}

.label-input-file:hover,
.input-file--hidden:focus + .label-input-file {
	outline: none;
	background: #c6c6c6;
	color: #414141;
}

.label-input-file:after {
	position: absolute;
	content: '';
	top: 0;
	right: -12px;
	width: 0;
	height: 0;
	border-width: 42px 14px 0 0;
	border-color: #eaeaea transparent transparent transparent;
	border-style: solid;
	pointer-events: none;
}

.label-input-file:hover:after,
.input-file--hidden:focus + .label-input-file:after {
	outline: none;
	border-color: #c6c6c6 transparent transparent transparent;
}

.input-button {
	border: none;
	padding: 10px 20px;
	color: #d1d1d1;
	position: relative;
	background: #000066;
	font-size: 1rem;
	cursor: pointer;
	transition: 500ms;
}

.input-button:hover {
	color: #ffffff;
	background: #000000;
}

.input-button:focus {
	outline: none;
	color: white;
	background: #000000;
}

.input-text {
	padding: 10px;
	background: #ffffff;
	border: none;
	color: #8e8e8e;
	font-weight: normal;
}

.input-text:focus {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::-webkit-input-placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::-ms-input-placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-text:focus::placeholder {
	outline: none;
	color: #333333;
	font-weight: bold;
}

.input-group {
	display: grid;
	grid-template-columns: 1fr;
}

.multifile-upload-group {
	display: grid;
	grid-template-columns: 1fr 102px;
}
#logo {
	color: white;
	font-weight: bold;
	font-size: 2rem;
	margin: 50px 0;
}

.error-message {
	padding: 20px;
	color: #999999;
	font-size: 1rem;
}

.bounding-box {
	position: absolute;
	box-shadow: inset 0 0 0 1px #14bff2;
	border: 0.9px solid #020259;
	z-index: 1;
}

.container {
	position: relative;
}

.image-demo {
	box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.25);
	width: 100%;
}

.image-panel {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	margin: 0 auto;
}

.info-demo {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-items: center;
	grid-row-gap: 10px;
	overflow-y: auto;
	height: 160px;
}

canvas {
	display: block;
}

